/* ==========================================================================
   NAVIGATION
   ========================================================================== */

/*
   Breadcrumb navigation links
   ========================================================================== */

.breadcrumbs {
    @include container;
    @include clearfix;
    margin-top: 0;
    margin-bottom: 0;
    padding-right: 2em;
    font-family: $sans-serif;
    animation: intro 0.3s both;
    animation-delay: 0.3s;

    @include breakpoint($large) {
        padding-right: 1em;
    }

    @include breakpoint($x-large) {
        max-width: $x-large;
    }

    ol {
        padding: 0;
        list-style: none;
        font-size: $type-size-6;

        @include breakpoint($large) {
            @include span(10 of 12 last);
            float: left;
            padding-left: 0;
        }

        @include breakpoint($x-large) {
            @include prefix(0.5 of 12);
            float: left;
            padding-left: 0;
        }
    }

    li {
        display: inline;
    }

    li a {
        display: inline;
        color: inherit;
    }

    .current {
        font-weight: bold;
    }
}

/*
   Post pagination navigation links
   ========================================================================== */

.pagination {
    @include full();
    @include clearfix();
    margin-top: 1em;
    padding-top: 1em;

    ul {
        margin: 0;
        padding: 0;
        list-style-type: none;
        font-family: $sans-serif;
    }

    li {
        display: block;
        float: left;
        margin-left: -1px;

        a {
            margin-bottom: 0.25em;
            padding: 0.5em 1em;
            font-family: $sans-serif;
            font-size: 14px;
            font-weight: bold;
            line-height: 1.5;
            text-align: center;
            text-decoration: none;
            color: mix(#fff, $gray, 25%);
            border: 1px solid $light-gray;
            border-radius: 0;

            &:hover {
                color: $link-color-hover;
            }

            &.current {
                color: #fff;
                background: $primary-color;
            }

            &.disabled {
                color: $light-gray;
                pointer-events: none;
                cursor: not-allowed;
            }
        }

        &:first-child {
            margin-left: 0;

            a {
                border-top-left-radius: $border-radius;
                border-bottom-left-radius: $border-radius;
            }
        }

        &:last-child {
            a {
                border-top-right-radius: $border-radius;
                border-bottom-right-radius: $border-radius;
            }
        }
    }

    /* next/previous buttons */
    &--pager {
        display: block;
        padding: 1em 2em;
        float: left;
        width: 50%;
        font-family: $sans-serif;
        font-size: $type-size-5;
        font-weight: bold;
        text-align: center;
        text-decoration: none;
        color: mix(#fff, $gray, 25%);
        border: 1px solid $light-gray;
        border-radius: $border-radius;

        &:hover {
            color: $link-color-hover;
        }

        &:first-child {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }

        &:last-child {
            margin-left: -1px;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        &.disabled {
            color: $light-gray;
            pointer-events: none;
            cursor: not-allowed;
        }
    }
}

.page__content + .pagination,
.page__meta + .pagination,
.page__share + .pagination,
.page__comments + .pagination {
    margin-top: 2em;
    padding-top: 2em;
    border-top: 1px solid $border-color;
}

/*
   Priority plus navigation
   ========================================================================== */

.greedy-nav {
    position: relative;
    min-width: 250px;
    margin-left: auto;
    margin-right: 0;
    text-align: center;
    align-items: center;
    display: flex;
    justify-items: center;
    margin-top: 0.8%;
    a {
        display: block;
        margin: 0;
        padding: 0 0;
        color: $head-nav-links;
        text-decoration: none;

        &:hover {
            color: $masthead-link-color-hover;
        }
    }

    button {
        position: absolute;
        height: 100%;
        right: 0;
        padding: 0 0.5rem;
        border: 0;
        outline: none;
        background-color: $primary-color;
        color: #fff;
        cursor: pointer;
    }

    .visible-links {
        margin-left: auto;
        margin-right: 2em;        
        display: table;

        li {
            display: table-cell;
            vertical-align: top;
            padding-top: 10px;

            &:first-child {
                font-weight: bold;

                a {
                    margin-left: 0;
                }
            }

            &:last-child {
                a {
                    margin-right: 0;
                }
            }
        }

        li:last-child {
            float: right;
            padding-top: 1px;
            padding-bottom: 2px;
            
        }

        a {
            position: relative;

            &:before {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                height: 4px;
                background: mix(#fff, $primary-color, 50%);
                width: 100%;
                transition: $global-transition;
                transform: scaleX(0); // hide
            }

            &:hover:before {
                transform: scaleX(1); // reveal
            }
        }
    }

    .hidden-links {
        position: absolute;
        top: 100%;
        right: 0;
        margin-top: 15px;
        padding: 5px;
        border: 1px solid $border-color;
        border-radius: $border-radius;
        background: #fff;
        box-shadow: 0 0 10px rgba(#000, 0.25);

        a {
            margin: 0;
            padding: 10px 20px;
            font-size: $type-size-5;

            &:hover {
                color: $masthead-link-color-hover;
                background: mix(#fff, $primary-color, 75%);
            }
        }

        &:before {
            content: "";
            position: absolute;
            top: -11px;
            right: 10px;
            width: 0;
            border-style: solid;
            border-width: 0 10px 10px;
            border-color: $border-color transparent;
            display: block;
            z-index: 0;
        }

        &:after {
            content: "";
            position: absolute;
            top: -10px;
            right: 10px;
            width: 0;
            border-style: solid;
            border-width: 0 10px 10px;
            border-color: #fff transparent;
            display: block;
            z-index: 1;
        }

        li {
            display: block;
            border-bottom: 1px solid $border-color;

            &:last-child {
                border-bottom: none;
            }
        }
    }
}

/*
   Navigation list
   ========================================================================== */

.nav__list {
    padding-top: 10px;
    font-size: 1.25rem;

    ul {
        margin-bottom: 1em;
    }

    a {
        display: block;
        padding: 0.125em 0;
        // color: $info-color;

        &:hover {
            text-decoration: underline;
        }
    }

    .active {
        margin-left: -0.5em;
        padding-left: 0.5em;
        padding-right: 0.5em;
        color: #fff;
        font-weight: bold;
        background: $primary-color;
        border-radius: $border-radius;

        &:hover {
            color: #fff;
        }
    }
}

.nav__list-item--selected {
    font-weight: bold;
    color: $light-blue;
}

.nav__title {
    margin: 0;
    padding: 0.5rem 1rem;
    font-family: $sans-serif-narrow;
    font-size: $type-size-5;
    font-weight: bold;
}

.nav__sub-title {
    display: block;
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    font-family: $sans-serif-narrow;
    font-size: $type-size-6;
    font-weight: bold;
    text-transform: uppercase;
    border-bottom: 1px solid $border-color;
}

/*
   Table of contents navigation
   ========================================================================== */

.toc {
    font-family: $sans-serif-narrow;
    color: $gray;
    text-transform: uppercase;
    letter-spacing: 1px;
    background-color: #fff;
    border: 1px solid $border-color;
    border-radius: $border-radius;
    box-shadow: $box-shadow;

    .nav__title {
        color: #fff;
        font-size: $type-size-6;
        background: $primary-color;
        border-top-left-radius: $border-radius;
        border-top-right-radius: $border-radius;
    }
}

.toc__menu {
    margin: 0;
    padding: 0;
    width: 100%;
    list-style: none;
    font-size: 0.8rem;

    a {
        display: block;
        padding: 0.5rem 1rem;
        color: $gray;
        font-size: $type-size-7;
        font-weight: bold;
        line-height: 1.5;
        border-bottom: 1px solid $border-color;

        &:hover {
            color: #000;
            background: $lighter-gray;
        }
    }

    > li:last-child {
        a {
            border-bottom: none;
        }
    }

    li ul > li a {
        padding-left: 2rem;
        font-weight: normal;
    }

    // hide sub sub links on small screens
    li > ul li {
        display: none;

        @include breakpoint($medium) {
            display: block;
        }
    }
}
